        //
        function toNum(a) {
            var a = a.toString();
            // var c=a.split(/\./);
            var c = a.split('.');
            var num_place = ["", "0", "00", "000", "0000"], r = num_place.reverse();
            for (var i = 0; i < c.length; i++) {
                var len = c[i].length;
                c[i] = r[len] + c[i];
            }
            var res = c.join('');
            return res;
        }
        function cpr_version(a, b) {
            var _a = toNum(a), _b = toNum(b);
            if (_a == _b) {
                console.log("" + a);
                self.is_version = a;
                self.versionType = true;
            }
            if (_a > _b) {
                console.log("" + a + "");
                self.versionType = false;
            }
            if (_a < _b) {
                self.versionType = true;
                console.log("" + b + "");
                self.is_version = b;
            }
        }

