        //recordcall
        this.recordsentence = function (sentence, recoder_call) {
            var words = sentence.split(" ");
            if (words.length <= 5) {
                self.recordword(words, recoder_call);

                return;
            }

            var type = "read_sentence";
            var data = { "sentence": sentence };
            self.recorder_call = recoder_call;
            self._record(type, data);
        };

        this._record = function (type, data) {
            self.record_current_tid = self.tid;
            var language = "en_us";
            var timeout = 6;
            self.log("========" + timeout)
            self.api.startSpeechEvaluation(type, language, data, timeout, self._record_call, self.tid.toString());
        };
        //
