        this._record_call = function (content) {
            if (self.record_current_tid != self.tid) {
                self.log("" + self.record_current_tid + "," + self.tid + ",");
                return;
            }

            var notify = content["notify"];
            if (notify == "onVolumeChanged") {
                //self.log("");
                var volume = content["body"]["volume"];
                self.recorder.volume = volume;
                self.log("", volume);
                self.recorder_call("volume", volume);
            } else if (notify == "onResults") {
                self.log("");
                self.recorder.volume = 0;
                var score = content["body"]["score"];
                self.recorder.scores = score;
                self.log("" + score);
                self.recorder_call("scores", score);
                //

            } else if (notify == "onBeginOfSpeech") {
                self.log("onBeginofSpeech");

            } else if (notify == "onCancel") {
                self.log("");
                self.recorder.volume = 0;
                self.recorder.scores = Math.random() * 0.5 + 1;
                if (self.recorder.scores < 1) {
                    self.recorder.scores = Math.random() * 0.5 + 1 * 20;
                }
                self.recorder_call("scores", self.recorder.scores);

            } else if (notify == "onError") {
                self.log("");
                if (!content["err"]) {
                    self.log("0", self.recorder.scores);
                    return;
                }
                self.recorder.volume = 0;
                self.recorder.scores = Math.random() * 0.5 + 1;
                if (self.recorder.scores < 1) {
                    self.recorder.scores = Math.random() * 0.5 + 1 * 20;
                }
                self.recorder_call("scores", self.recorder.scores);

            } else {
                self.log(content);
                self.log("");
            }
        };

