(function (window, game) {
    var state_name = "05";
    var images_path = "./states/" + state_name + "/images/";
    var audio_path = "./states/" + state_name + "/images/";
    var BackgroundColor = 0xffffff; //
    game.state.add(state_name, function () {
        var self = this;
        this.buttonId = null;
        var bFlga = true;
        var temr = 10;
        var anwArr = new Array();//;
        var count_btn = 0; //
        var tijiao = false; //
        this.init = function (data) {
            game.scale.pageAlignHorizontally = true; //
            game.scale.pageAlignVertically = true; //
            game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
            game.stage.setBackgroundColor(BackgroundColor);
            if (data === undefined) {

            }
            this.data = data;
            this.fensu = 0;
            this.wrong = false;  //
            anwArr = new Array();//;
            bFlga = true;
            temr = 10;
            anwArr = new Array();//;
            count_btn = 0; //
            tijiao = false; //
            this.body_group = null
            this.game_but = null;  //GO
        };

        this.preload = function () {
            /**/
            // /**/
            // for(var i=0;i<this.data.body.length;i++){
            //     game.load.image(game.stateIndex+"body_image"+i,this.data.body[i].image);
            // }
            // /**/
            // /*****/
            // if(this.data.questionImage!=""&&this.data.questionImage!=undefined){
            //     game.load.image("questionImage",this.data.questionImage);
            // }
        };

        this.create = function () {
            var other_images = this.data.other_images;

            if (other_images) {
                for (var tem_i = 0; tem_i < other_images.length; tem_i++) {

                    other_images[tem_i]['image'] !== '' ? game.add.image(other_images[tem_i]['pos']['x'], other_images[tem_i]['pos']['y'], game.stateIndex + 'other_images' + tem_i + 'temp') : null;
                }
            }

            var self = this;
            var title_text = game.add.text(this.data.text_pos.x, this.data.text_pos.y, this.data.title, { fill: this.data.fontColor, fontSize: this.data.fontSize, align: "center" });

            title_text.setTextBounds(0, 0, 250, 40); //
            title_text.wordWrapWidth = 900;//

            title_text.wordWrap = true;
            //
            if (this.data.q_image != "" && this.data.q_image != undefined) {
                if (this.data.pos != "" && this.data.pos != undefined) {
                    game.add.image(this.data.pos.x, this.data.pos.y, game.stateIndex + "questionImage");
                }

            }

            if (this.data.image != "" && this.data.image != undefined) {

                var s = game.add.image(this.data.text_pos.x, this.data.text_pos.y, game.stateIndex + 'titleImage');

            }
            /*====*/
            self.body_group = game.add.group();
            for (var i = 0; i < this.data.body.length; i++) {
                if (this.data.body[i].pos != "") {
                    self.body_group.add(game.add.button(this.data.body[i].pos.x, this.data.body[i].pos.y, game.stateIndex + "body_image" + i, self.onClick, this));
                } else {
                    self.body_group.add(game.add.button(200 + i * 200, 400, game.stateIndex + "body_image" + i, self.onClick, this));
                }
                var current = self.body_group.children[i];
                //current.width =  current.width*0.6;
                //current.height =current.height*0.5;
                current.id = i;
                current.have_image = null;
                current.isFlag = false;
                current.alpha = 0.5;

            }
            if (!gameManager.is_teacher) {
                /**====**/
                if (this.data.ok_btnPos != "" && this.data.ok_btnPos != undefined) {
                    self.queren = game.add.button(this.data.ok_btnPos.x, this.data.ok_btnPos.y, "OK", self.check_answer, this, 1, 0, 1, 0);
                } else {
                    self.queren = game.add.button(450, 620, "OK", self.check_answer, this, 1, 0, 1, 0);
                }
            }

            //
            if (game.is_teacher) {
                //
                var c = 0x6dc7eb;
                var q_text_length = 4;
                var bg = game.add.image(60, 550, 'go');
                this.game_but = game.add.text(5, 10, "GO", { fill: "#000000", fontSize: 60, fontWeight: "normal", boundsAlignH: "center", boundsAlignV: "middle" });
                this.game_but.setTextBounds(35, 38, 16 * q_text_length / 2, 30);
                bg.addChild(this.game_but);
                this.game_but.inputEnabled = true;
                var self = this;
                this.game_but.events.onInputUp.add(function () {
                    if (!bFlga) {
                        return false; //
                    }
                    if (bFlga) {
                        bFlga = false;
                        var t = 9;
                        var daojis = setInterval(function () {
                            t--;
                            if (t === 0) {
                                self.game_but.text = "END";
                                self.game_but.fontSize = 40;
                                c = 0xF5F5F5;
                                self.game_but.style.color = 0xCD0000;
                                clearInterval(daojis);
                                bFlga = true;
                                return false;
                            }
                            self.game_but.text = t + "s";
                        }, 1000);
                    }


                    gameManager.startTesting();
                });


            } else {
                game.show_mengban();
                //  
                game.progressBar = this.add_progress_bar(0, 0, 758);
                game.progressBar.alpha = 0;
            }
            //
            gameCommon();
        }
        var timerBarHeight = 10; //timer bar height
        var timerBarColour = 0x43CD80; //timer bar colour 
        /*!
         *   Progress bar
         */
        this.add_progress_bar = function (anime, x, y) {
            var progress = game.add.graphics(0, 0);
            progress.beginFill(timerBarColour);//160-1
            progress.drawRect(0, 0, game.width, timerBarHeight);//
            progress.endFill();
            var sprite = game.add.sprite(x, y, progress.generateTexture());
            progress.destroy();
            return sprite;
        }
        var bFlag = true;
        this.onClick = function (button) {
            if (game.is_teacher) {
                return false;
            }

            if (tijiao) {
                console.log("");
                return false;
            }


            if (button.have_image != null) {
                button.have_image = null;//
                button.isFlag = false; //
                // button.frame=0;
                console.log('1', button.isFlag);
                if (button.id + 1 > anwArr.length) {
                    console.log('==2=', 1);
                    anwArr.splice(anwArr.length - 1, 1);
                } else {
                    console.log('==3=', button.id);
                    for (var i = 0; i < anwArr.length; i++) {
                        if (anwArr[i] == button.id) {
                            anwArr.splice(i, 1);
                        }
                    }

                }
                button.children[0].destroy()//
                console.log('===', anwArr);
                return;
            }

            if (button.have_image == null) {
                console.log('2', button.isFlag);
                button.isFlag = true;
                // button.frame=1;
                button.have_image = button.id;
                anwArr.push(button.have_image);
                game.add.tween(button).to({ width: button.width + 20, height: button.height + 20 }, 100, null, true, 0, 1, true);
                setTimeout(function () {
                    //button.width =   button.width+40;
                    //button.height = button.height+40;
                    var garphics = game.add.graphics(0, 0);
                    var xxk = "0xA2FCFA";
                    if (button.id == 0) {
                        xxk = "0xF5E843";
                        garphics.lineStyle(10, xxk);
                    } else if (button.id == 1) {
                        xxk = "0xec003a";
                        garphics.lineStyle(10, xxk);
                    } else if (button.id == 2) {
                        xxk = "0xA2FCFA";
                        garphics.lineStyle(10, xxk);
                    } else {
                        garphics.lineStyle(10, xxk);
                    }
                    garphics.drawRoundedRect(button.x, button.y, button.width - 20, button.height - 20);
                    var sub = button.addChild(game.add.image(0, 0, garphics.generateTexture()));//imgae addChild 
                    garphics.destroy(); //
                }, 100);
                console.log("", anwArr);
            }



        };

        //
        this.check_answer = function (btn) {
            //

            if (game.is_teacher) {
                return false;
            }
            // btn.alpha = 0;
            if (tijiao) {
                console.log("");
                return false;
            }
            if (self.data.answer.length == anwArr.length) {

                for (k in anwArr) {
                    var id = anwArr[k];
                    if (!self.check_sub(id)) {//ID false
                        self.fensu = 0;
                        self.wrong = true;
                        console.log("", id);
                        for (s in self.data.body) {
                            //var l = self.data.body[s];
                            if (!self.check_sub(s)) {
                                // console.log(s);
                                //var uid = self.data.answer[s];
                                if (this.data.body[s].pos != "") {
                                    game.add.image((this.data.body[s].pos.x + 20), this.data.body[s].pos.y + 25, "choice", 2);
                                } else {
                                    game.add.image(510, 115, "choice", 2);
                                }

                            } else {
                                if (this.data.body[s].pos != "") {
                                    game.add.image((this.data.body[s].pos.x + 20), this.data.body[s].pos.y + 25, "choice", 1);
                                } else {
                                    game.add.image(510, 115, "choice", 2);
                                }
                            }


                        }
                        clearInterval(game.temp_t);
                        self.submitScore(self.fensu, 4);
                        console.log("");
                        //
                        clearInterval(game.temp_t);
                        // self.submitScore(self.fensu,4);
                        return false;
                    }

                }
                for (l in self.data.body) {
                    //var l = self.data.body[s];
                    if (!self.check_sub(l)) {
                        // console.log(s);
                        //var uid = self.data.answer[s];
                        if (this.data.body[l].pos != "") {
                            game.add.image((this.data.body[l].pos.x + 20), this.data.body[l].pos.y + 25, "choice", 2);
                        } else {
                            game.add.image(510, 115, "choice", 2);
                        }

                    } else {
                        if (this.data.body[l].pos != "") {
                            game.add.image((this.data.body[l].pos.x + 20), this.data.body[l].pos.y + 25, "choice", 1);
                        } else {
                            game.add.image(510, 115, "choice", 2);
                        }
                    }


                }
                clearInterval(game.temp_t);
                // self.submitScore(self.fensu,4);
                self.fensu = 100;
                //
                console.log("");
                clearInterval(game.temp_t);
                self.submitScore(self.fensu, 1);
            } else {
                self.fensu = 0;
                self.wrong = true;
                //  
                for (s in self.data.body) {
                    //var l = self.data.body[s];
                    if (!self.check_sub(s)) {
                        // console.log(s);
                        //var uid = self.data.answer[s];
                        if (this.data.body[s].pos != "") {
                            game.add.image((this.data.body[s].pos.x + 20), this.data.body[s].pos.y + 25, "choice", 2);
                        } else {
                            game.add.image(510, 115, "choice", 2);
                        }

                    } else {
                        if (this.data.body[s].pos != "") {
                            game.add.image((this.data.body[s].pos.x + 20), this.data.body[s].pos.y + 25, "choice", 1);
                        } else {
                            game.add.image(510, 115, "choice", 2);
                        }
                    }


                }
                clearInterval(game.temp_t);
                self.submitScore(self.fensu, 4);
                console.log("");
            }
            // btn.frame= 1;
            self.queren.frame = 1;
            tijiao = true;

        };
        this.check_sub = function (id) {
            for (n in self.data.answer) {
                var aid = self.data.answer[n];
                if (aid == id) {
                    return true;
                }
            }

            return false;
        };

        /***======****/
        this.submitScore = function (scores, animation) {
            if (self.wrong) {
                animation = 4;
            }
            // gameManager.addRecord(scores)//
            // gameManager.sendScore(Math.round(scores),100,animation);
            gameManager.stars(Math.round(scores)); //demo
            clearInterval(game.temp_t);
            if (animation == 1) {
                log("")
                log(scores)
                ScoreAnimtions(scores) //
                return
            }
            if (animation == 4) {
                log("=====")
                gameErrorAnimations()//
            }

        }
        /**====**/
        this.answers_startgame = function () {
            var c = 0x6dc7eb;
            if (game.is_teacher) {
                if (this.game_but != null) {
                    var self = this;
                    if (!bFlga) {
                        return false; //
                    }
                    if (self.game_but.text != "END") {
                        if (bFlga) {
                            bFlga = false;
                            var t = 9;
                            var daojis = setInterval(function () {
                                t--;
                                if (t === 0) {
                                    self.game_but.text = "END";
                                    self.game_but.fontSize = 40;
                                    c = 0xF5F5F5;
                                    self.game_but.style.color = 0xCD0000;
                                    clearInterval(daojis);
                                    bFlga = true;
                                    return false;
                                }
                                self.game_but.text = t + "s";
                            }, 1000);
                        }
                    }
                    if (self.game_but.text != "END") {
                        gameManager.startTesting();
                    }

                }
            }
        }
        /***  ***/

        //
        this.startgame = function (time_out) {
            if (time_out == undefined) {
                time_out = 9;
            }
            for (var i = 0; i < this.body_group.length; i++) {
                var current = this.body_group.children[i];
                current.alpha = 1; //
            }
            game.progressBar.alpha = 1;
            game.show_mengban(true);
            var timerStart = 8000; //timer start number  10
            game.add.tween(game.progressBar).to({ width: 0, alpha: 0 }, timerStart, Phaser.Easing.Linear.None, true).onComplete.add(function () {
                tijiao = true; //
            }); // 
            game.temp_t = setInterval(function () {
                time_out--;
                temr--;
                if (time_out === 0) {
                    bFlag = false;
                    tijiao = true;
                    clearInterval(game.temp_t);
                    self.submitScore(0, 3);
                    for (var s in self.data.body) {
                        //var l = self.data.body[s];
                        if (!self.check_sub(s)) {
                            console.log(s);
                            if (this.data.body[s].pos != "") {
                                game.add.image((this.data.body[s].pos.x + 20), this.data.body[s].pos.y + 25, "choice", 2);
                            } else {
                                game.add.image(510, 115, "choice", 2);
                            }

                        } else {
                            if (this.data.body[s].pos != "") {
                                game.add.image((this.data.body[s].pos.x + 20), this.data.body[s].pos.y + 25, "choice", 1);
                            } else {
                                game.add.image(510, 115, "choice", 2);
                            }
                        }
                    }


                }

            }, 1000);
            console.log("");

        }
    });


})(window, game);